.skills {
  background-color: #FFF;
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  width: 100%;
  justify-content: center;
}

.skills div {
  display: inline-flex;
  flex-direction: column;
}

.skills div button {
  min-width: 100px;
  min-height: 100px;
  border: none;
  outline: none;
  font-size: 3rem;
}

.skills div button:hover + span {
  opacity: 1;
}

.skills span:hover {
  opacity: 1;
}

.skills span {
  transform: translate(0, -100%);
  position: relative;
  top: 0;
  font-size: 1rem;
  left: 0;
  bottom: 0;
  background-color: #FF4040;
  border-top: 2px solid #FFF;
  color: #FFF;
  text-align: center;
  padding: 7px 5px;
  box-shadow: 0 0px 5px 2px rgba(0,0,0,.5);
  opacity: 0;
  transition: 50ms;
}

.skills div:nth-of-type(1) button {
  background-color: #F7DF1E;
  color: #000;
}

.skills div:nth-of-type(2) button {
  background-color: #61DAFB;
  color: #FFF;
}

.skills div:nth-of-type(3) button {
  background-color: #F16528;
  color: #FFF;
}

.skills div:nth-of-type(4) button {
  background-color: #4A8FE2;
  color: #FFF;
}

.skills div:nth-of-type(5) button {
  background-color: #73A1FB;
  color: #FFF;
}

.skills div:nth-of-type(6) button {
  background-color: #FF4040;
  color: #FFF;
}
