.home-header {
  width: 100%;
  display: block;
  position: relative;
  background-color: #000;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

.home-header::before {
  content: "";
  background: url(./img/header-background.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;
  background-attachment: fixed;
  background-position: center;
}

.home-header div {
  position: relative;
  z-index: 2;
}

.home-header img {
  height: 150px;
  border-radius: 100%;
  border: 5px solid #FFF;
}

.home-header span {
  display: block;
  padding: 0;
  text-align: center;
  color: #FFF;
  font-size: 1.8rem;
  margin-top: 5px;
  text-shadow: 2px 2px rgba(0,0,0,.5);
}
