nav {
  width: 100%;
  background-color: #FFF;
  box-shadow: 0 5px 5px -5px rgba(0,0,0,.5);
  z-index: 10;
  position: fixed;
  padding: 10px;
  margin-top: 0;
  top: 0;
}

nav span {
  text-align: center;
  line-height: 50px;
}

nav ul {
  display: inline-flex;
  float: right;
  margin: 0;
  margin-right: 20px;
}

nav ul li {
  color: #FF4040;
  margin-left: 20px;
  margin-right: 20px;
  list-style: none;
  font-size: 1rem;
  border-radius: 5px;
  padding: 7px 10px;
  transition: 50ms;
}

nav ul li:hover {
  cursor: pointer;
  background-color: #FF4040;
  color: #FFF;
}
