.socials {
  position: relative;
  width: 100%;
  text-align: center;
  color: #FF4040;
  z-index: 2;
  box-shadow: 0 0px 5px 2px rgba(0,0,0,.5);
}

.socials div {
  margin-top: 30px;
  margin-bottom: 30px;
  display: inline-flex;
  flex-direction: column;
}

.socials div a {
  color: #FF4040;
  font-size: 3rem;
  margin-left: 20px;
  margin-right: 20px;
}

.socials div a:hover + span {
  transform: translate(0, 0);
  opacity: 1;
}

.socials div span {
  transform: translate(0, -10px);
  opacity: 0;
  transition: .1s;
}
